<template>
    <div class="bodyCon08">
	<div class="students">
		
		  <div id="four_flash">
			<div class="flashBg">
				<ul class="mobile">
					<li v-for="(blogItem) in $store.state.BlogItemData" :key="blogItem.id" @click="gotoArticle(blogItem.id)">
						<img  :src="blogItem.Imgsrc" />
						<dd class="Relative-title">{{blogItem.BigTitle}}</dd>
						<p  class="Relative-brief">{{blogItem.brief}}</p>
						<a href=""></a>
					</li>
				</ul>
			</div>
			<div class="but_left"><img src="../../../assets/img/images/qianxleft.png" /></div>
			<div class="but_right"><img src="../../../assets/img/images/qianxr.png" /></div>
		  </div>
		  
	</div>
</div>
</template>

<script>
import $ from 'jquery'
import axios from 'axios'
export default {
    methods:{
    gotoArticle(ItemId){
            this.$router.push(`/blog/blogItem/${ItemId}`);

        }
    }, created(){
       axios.get('/data/Blogdata.json').then(res=>{
            this.$store.commit('setBlogdata',res.data.BlogItemData);
            
        })
    },
    mounted(){
        // 移动函数
        var _index5=0;
        $("#four_flash .but_right img").click(function(){
            _index5++;
            var len=$(".flashBg ul.mobile li").length;
            if(_index5+5>len){
                $("#four_flash .flashBg ul.mobile").stop().append($("ul.mobile").html());
            }
            $("#four_flash .flashBg ul.mobile").stop().animate({left:-_index5*326},1000);
            });

            
        $("#four_flash .but_left img").click(function(){
            if(_index5==0){
                $("ul.mobile").prepend($("ul.mobile").html());
                $("ul.mobile").css("left","-1380px");
                _index5=6
            }
            _index5--;
            $("#four_flash .flashBg ul.mobile").stop().animate({left:-_index5*326},1000);
            });
    },data () {
        return {
            RelativeArcile:[]
        }
    }
}

</script>

