    mounted(){
            window.onscroll = ()=>{
                let scrollTop = document.documentElement.scrollTop ;//滚动条距离HTML的高度
                let viewBg = document.querySelector('.big-bg'); //获取bg的dom结构
                let heder = document.querySelector('.nav');//获取header的dom结构
