                        rowBg2.style['background-position-y'] = -520 + scrollTop/2 + 'px' //控制bg2的背景移动
                    }
                }
                //控制 头导航 背景颜色\阴影的变化
                if(scrollTop>200){
                    heder.style.background = '#000';
                    heder.style ['box-shadow'] = '0 2px 20px #000000';                
                }else{
                    heder.style.background = 'transparent';
                    heder.style ['box-shadow'] = '0 0px 0px ';                
                }
                //控制 bg3的背景移动
                if(scrollTop > 5.5*NowViewBg && scrollTop < 7.5 * NowViewBg){
                    rowBg3.style['background-position-y'] = -1600 + scrollTop/4 + 'px' //控制bg3的背景移动
                }
            }

    },
