import Vue from 'vue'
import Router from 'vue-router'
// import PageTwo from '@/views/detail-p1/PageTwo.vue'
import Home from '@/views/Home.vue'
import Music from '@/views/Music.vue'
import Photo from '@/views/Photo.vue'
import Contact from '@/views/Contact.vue'
import Blog from '@/views/Blog.vue'
import BlogDetail from '@/views/BlogContent/BlogDetail/BlogDetail.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
      {
        path:'/',
      },
      {
        path:'/home',
        component:Home,
      },{
        path:'/music',
        component:Music
      },{
        path:'/photo',
        component:Photo
      },{
        path:'/blog',
        component:Blog
      },{
        path: '/blog/blogItem/:BlogItem',
        name: 'BlogItem',
        component: BlogDetail
      },{
        path:'/contact',
        component:Contact
      }

  ]
})
