import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      photoList: [],
      skillList:[],
      BlogItemData:[],
  },
  mutations: {
    //照片墙数据
    setPhotoList(state, list) {
      state.photoList = list;
    }, 
    //技能照片数据
    setSkillList(state, list) {
      state.skillList = list;
    },
    //blog的item数据
    setBlogdata(state, list) {
      state.BlogItemData = list;
    },
  },
  actions: {

  }
})
