<template>
    <div class="slideY">
        <div class="warpper clearfix">
            <div class="slideY-title">
                <h1>My Main Skills</h1>
            </div>
            <transition name="moveY">
                <ul class="slideY-content clearfix">
                    <li v-for="logo in $store.state.skillList" :key="logo.src" >
                        <a href="">
                            <img :src="logo.src" alt="">
                        </a>
                    </li>
                </ul>
            </transition>
        </div>
       
        <ul class="control-skill-button clearfix">
            <li class="prev" @click="PrevmoveY">&lt;</li>
            <li class="next" @click="NextmoveY">&gt;</li>
        </ul>
        
    </div>
</template>

<script>
import axios from 'axios'
export default {
    methods:{
        //控制水平滚动条
        PrevmoveY(){
            let slideYDom = document.querySelector('.slideY-content');
            slideYDom.scrollTo(-500, slideYDom.scrollTop);
        } ,
        NextmoveY(){
            let slideYDom = document.querySelector('.slideY-content');
            slideYDom.scrollTo(500, slideYDom.scrollTop);
        }
    },
    mounted(){
        axios.get('/data/skilLogo.json').then(res=>{
            this.$store.commit('setSkillList',res.data.logoData);
            //  console.log(this.$store.state.skillList)

        })
    }
}
</script>

